<template>
  <div
    class="flex column row"
  >
    <label
      class="label mr-2"
      :for="qid"
      style="white-space: nowrap"
    >Link do vdeo:</label>
    <Input
      :id="qid"
      v-model="answer"
      type="text"
      :name="qid"
      required
      @input="update()"
      @invalid="$emit('uncollapse');"
    />
    <FormInvalidator 
      :valid="getId(this.answer) !== undefined"
      :start-invalid="triedSubmit"
      :invalid-string="'Deve introduzir um link vlido'"
      style="margin-bottom: 0px !important"
      @invalid="$emit('uncollapse'); triedSubmit = true;"
    />
  </div>
</template>

<script>
import Input from "@/components/Input";
import FormInvalidator from "@/components/FormInvalidator";
const rx = /^.*(?:(?:youtu\.be\/|v\/|vi\/|u\/\w\/|embed\/)|(?:(?:watch)?\?v(?:i)?=|&v(?:i)?=))([^#&?]*).*/;
export default {
  name: "YouTubeField",
  props: ["question", "qid", "value", "properties"],
  emits: ["update", "uncollapse"],
  components: {Input, FormInvalidator},
  data() {
    return {
      answer: this.properties.content,
      triedSubmit : false
    };
  },
  methods: {
    update() {
      this.$emit("update", {
        field_type: "youtube_exposition",
        content: this.getId(this.answer),
      });
    },
    getId(url) {
      if (url) {
        const r = url.match(rx);
        if (r)
          return r[1];
      }
      return undefined;
    }
  },
};
</script>
