<template>
  <div
    v-if="form"
    :key="form"
    class="box"
  >
    <h1 class="title">
      Criao de novo formulrio
    </h1>
    <form
      id="form-area"
      ref="form"
      @submit.prevent="submit()"
    >
      <Input
        id="title"
        v-model="form.title"
        placeholder="Ttulo do formulrio"
        class="input is-medium"
        type="text"
        autofocus
        required
        @input="changes++"
      />
      <hr>
      <div class="level">
        <div class="level-item control has-icons-left">
          <div
            class="select"
            :class="{'is-danger' : typeInvalid && form.type === ''}"
          >
            <select
              id="type"
              v-model="form.type"
              required
              :disabled="form.editable == false || (form.id && ((form.signup_uses != 0 && form.signup_use !== undefineds) || (form.feedback_uses != 0 && form.feedback_uses !== undefined) != 0))"
              @invalid="typeInvalid = true"
            >
              <option
                value=""
                disabled
              >
                Tipo de Formulrio
              </option>
              <option
                v-for="(formType, formTypeId) in form_types"
                :key="formType"
                :value="formTypeId"
              >
                Formulrio de {{ formType.name }}
              </option>
            </select>
            <span
              v-show="!(form.type >= 1 && form.type <=3)"
              class="icon is-left"
            >
              <i class="fas fa-mouse-pointer" />
            </span>
            <span
              v-show="form.type == 1"
              class="icon is-left"
            >
              <i class="fas fa-user-plus" />
            </span>
            <span
              v-show="form.type == 2"
              class="icon is-left"
            >
              <i class="fas fa-calendar-plus" />
            </span>
            <span
              v-show="form.type == 3"
              class="icon is-left"
            >
              <i class="fas fa-star" />
            </span>
          </div>
        </div>
        <div
          class="level-item flex column ca vca"
        >
          <FormInvalidator 
            :valid="form.redirect_delay >= 0"
            :invalid-string="'O atraso de redireo no pode ser negativo.'"
            @invalid="triedSubmit = true;"
          />
          <Dropdown
            :text="'Opes avanadas'"
            :valid="!(triedSubmit && form.redirect_delay < 0)"
          >
            <div
              class="dropdown-item"
              style="white-space:nowrap"
            >
              <label
                class="label"
                for="redirect"
              >Pgina de redireo aps sucesso</label>
              <Input
                id="redirect"
                v-model="form.redirect"
                placeholder="Pgina para redireo"
                class="input"
                type="text"
                @input="changes++"
              />
            </div>
            <div
              class="dropdown-item"
              style="white-space:nowrap"
            >
              <label
                class="label"
                for="redirect_delay"
              >Atraso de redireo (segundos)</label>
              <Input
                id="redirect_delay"
                v-model="form.redirect_delay"
                placeholder="Atraso de redireo"
                class="input"
                type="number"
                step="1"
                min="0"
                @input="changes++"
              />
            </div>
            <hr
              v-if="form.editable != false && !(form.id && ((form.signup_uses != 0 && form.signup_uses !== undefined) || (form.feedback_uses != 0 && form.feedback_uses !== undefined) != 0))"
              class="dropdown-divider"
            >
            <div
              v-if="form.editable != false && !(form.id && ((form.signup_uses != 0 && form.signup_uses !== undefined) || (form.feedback_uses != 0 && form.feedback_uses !== undefined) != 0))"
              class="dropdown-item"
              style="white-space:nowrap"
            >
              <Switch 
                v-model="form.form.hide_section_numbers"
                :text="'Esconder nmeros de seces'"
                @update:modelValue="changes++"
              />
            </div>
            <div
              v-if="form.editable != false"
              class="dropdown-item"
              style="white-space:nowrap"
            >
              <Switch 
                v-model="form.form.hide_question_numbers"
                :text="'Esconder nmeros de perguntas'"
                @update:modelValue="changes++"
              />
            </div>
          </Dropdown>
        </div>
      </div>
      <hr>
      <div v-if="form.editable != false && !(form.id && ((form.signup_uses != 0 && form.signup_uses !== undefined) || (form.feedback_uses != 0 && form.feedback_uses !== undefined) != 0))">
        <draggable
          v-model="form.form.sections"
          item-key="id"
          group="sections"
          tag="transition-group"
          :component-data="{
            name: 'list',
            tag: 'div'
          }"
          :move="move()"
          v-bind="dragOptions"
          handle=".section-handle"
          class="draggable-section"
          @end="changes++;"
        >
          <template #item="{element, index}"> 
            <div
              :ref="'section_' + index"
              class="container is-flex mb-5 list-item draggable-section m-0 p-0"
            >
              <div
                class="panel is-info is-flex-grow-1"
                style="height: fit-content;"
              >
                <div class="panel-heading mb-2 section-handle draggable level prevent-overflow">
                  <h2 class="level-left is-size-3">
                    <template v-if="!form.form.hide_section_numbers">
                      {{ index+1 }}.
                    </template>
                    {{ element.section_name === '' ? 'Seco ' : element.section_name }}
                  </h2>
                  <div class="field level-right is-hidden-mobile">
                    <div class="control level-item">
                      <button
                        type="button"
                        class="button is-danger is-medium"
                        aria-label="Apagar seco"
                        @click="form.form.sections.splice(index, 1); changes++;"
                      >
                        <span class="icon">
                          <i class="fas fa-trash-alt" />
                        </span>
                      </button>
                    </div>
                    <div class="control level-item">
                      <button
                        v-show="form.form.hidden_elements[element.id]"
                        type="button"
                        class="button is-medium"
                        aria-label="Expandir seco"
                        @click="form.form.hidden_elements[element.id] = undefined"
                      >
                        <span class="icon">
                          <i class="fas fa-chevron-down" />
                        </span>
                      </button>
                      <button
                        v-show="!form.form.hidden_elements[element.id]"
                        type="button"
                        class="button is-medium"
                        aria-label="Minimizar seco"
                        @click="form.form.hidden_elements[element.id] = true"
                      >
                        <span class="icon">
                          <i class="fas fa-chevron-up" />
                        </span>
                      </button>
                    </div>
                  </div>
                </div>
                
                <div class="panel-block field is-hidden-tablet level">
                  <button
                    type="button"
                    class="button is-danger is-medium is-flex-grow-1"
                    aria-label="Apagar seco"
                    @click="form.form.sections.splice(index, 1); changes++;"
                  >
                    <span class="icon">
                      <i class="fas fa-trash-alt" />
                    </span>
                  </button>
                </div>
                <transition
                  name="component-fade" 
                  mode="out-in"
                >
                  <div
                    v-show="!form.form.hidden_elements[element.id]"
                    class="panel-block control is-flex-grow-1 fade-item"
                  >
                    <Input
                      v-model="element.section_name"
                      placeholder="Ttulo da seco"
                      class="input is-medium"
                      required
                      type="'text'"
                      @input="changes++"
                      @invalid="form.form.hidden_elements[element.id] = undefined"
                    />
                  </div>
                </transition>
                <CreatorQuestions 
                  :ref="'section_' + index + '_questions'"
                  :question-section="element"
                  :collapsed="form.form.hidden_elements[element.id]"
                  :hide-question-numbers="form.form.hide_question_numbers"
                  @uncollapse="form.form.hidden_elements[element.id] = undefined"
                  @update="form.form.sections[element.id] = $event; changes++" 
                />
              </div>
            </div>
          </template>
        </draggable>
        <div class="flex row ca mb-6">
          <button
            type="button"
            class="button is-primary"
            @click="addSection(); changes++;"
          >
            Adicionar seco
          </button>
        </div>
      </div>
      <div
        v-else
        class="subtitle mb-4 ml-2"
      >
        Este formulrio j tem respostas ou est a ser usado, pelo que as suas perguntas no podem ser editadas.
      </div>
      <hr>
      <div class="panel is-primary">
        <div class="panel-heading">
          Mensagem de sucesso
        </div>
        <div class="panel-block">
          <tiptap
            v-model="form.submit_message"
            style="width: 100%"
            class="mb-4"
            @update="this.form.submit_message = $event; changes++"
          />
        </div>
      </div>
      <Submit
        style="width: fit-content;"
        :hide="changes == 0"
        float-bottom="true"
        :error="response && response.id === undefined"
        error-message="Houve um erro na submisso do formulrio."
        success-duration="3000"
        :success-message="success"
        :loading="loading"
        submit-text="Guardar"
        red-green="true"
        cancel-confirmation="Pretende eliminar as suas alteraes?"
        submit-confirmation="Pretende guardar as suas alteraes?"
        cancel-conf-button="Eliminar"
        submit-conf-button="Guardar"
        @leave="cancel()"
        @dismissed="this.response = undefined; this.success = undefined"
      />
    </form>
    <!-- <div v-if="false">
      Informao a ser enviada:
      <pre class="mt-1 mb-4"><code>{{ JSON.stringify(form, null, '  ') }}</code></pre>
      <template v-if="response">
        Resposta do servidor: 
        <pre class="mt-1"><code>{{ JSON.stringify(response, null, '  ') }}</code></pre>
      </template>
    </div> -->
  </div>
</template>

<script>
import draggable from "vuedraggable";
import Submit from "@/components/Submit";
import Switch from "@/components/Switch";
import CreatorQuestions from "@/components/CreatorQuestions";
import FormInvalidator from "@/components/FormInvalidator"
import Input from "@/components/Input";
import Dropdown from "@/components/Dropdown";
import Tiptap from '@/components/Tiptap.vue'
import { API } from "@/http-common";
import { getCookie } from "@/common";

export default {
  name: "CreateResponse",
  components : {Submit, Switch, CreatorQuestions, draggable, Dropdown, Input, FormInvalidator, Tiptap},
  data() {
    return {
      response: undefined,
      form_types : [],
      form: undefined,
      new_field : "none",
      selecting_field : [],
      loading: undefined,
      triedSubmit: false,
      typeInvalid: false,
      success : undefined,
      changes : 0,
    };
  },
  created() {
    document.title = "Criao de Formulrio"
    API.get("formType/")
      .then((response) =>
        this.form_types = response.data
      );
    if (this.$route.params.id) {
      API.get("form/".concat(this.$route.params.id))
        .then((response) => {
            this.form = response.data;
            if (this.form.form.hidden_elements === undefined)
              this.form.form.hidden_elements = {}
            }
        );
    }
    else
      this.form = {
        title : "",
        type: "",
        submit_message : "Formulrio submetido com sucesso!",
        redirect: "/pre_events/consult_events/",
        redirect_delay : 3,
        form : {
          sections : [{"section_name" : "", "id" : this.$store.getters.getUid, "questions" : []}],
          hidden_elements: {},
          hide_section_numbers: false,
          hide_question_numbers: false,
        }
      };
  },
  methods: {
    move() {
      return (event) => {
        return (event.related.classList.contains("draggable-section"))
      }
    },
    cancel() {
      this.loading = true;
      if (this.$route.params.id) {
        API.get("form/".concat(this.$route.params.id))
          .then((response) => {
              this.form = response.data
              this.changes = 0;
              this.loading = undefined;
          }
        );
      }
      else
        this.$router.back();
    },
    addSection() {
      var init = {"section_name" : "", "id" : this.$store.getters.getUid, "questions" : [], "hidden_elements" : {}}
      this.form.form.sections.push(init);
      let last = 'section_'.concat(this.form.form.sections.length-1);
      this.$nextTick(() => this.$refs[last].scrollIntoView({behavior: 'smooth'}));
    },
    submit() {
      if (this.$refs.form.checkValidity()) {
        this.loading = true;
        setTimeout(this.postSubmission.bind(this), 0);
      }
    },
    postSubmission() {
      var method;
      var url;
      var successMessage;
      if (this.form.id === undefined)  {
          method = "post";
          url = "/form/";
          successMessage = "O formulrio foi criado com sucesso. Pode continuar a editar.";
      }
      else {
        method = "put";
        url = "/form/".concat(this.form.id).concat("/");
        successMessage = "O formulrio foi editado com sucesso. Pode continuar a editar.";
      }
      var options = {
        method : method,
        url : url,
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: this.form,
        transformResponse: [
          async (data) => {
            this.loading = undefined;
            try {
              this.response = JSON.parse(data);
              this.form = this.response;
              this.success = successMessage;
              this.changes = 0;
            } catch (e) {this.response = data;}
            return this.response;
          },
        ],
      };
      API(options);
    },
    removeEmptySections() {
      for (var i = 0; i < this.form.form.sections.length; /* do nothing */) {
        if (this.form.form.sections[i].questions.length == 0)
          this.form.form.sections.splice(i, 1);
        else
          i++;
      }
    }
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      };
    }
  }
};
</script>

<style lang="scss">
  .round {
    border-radius: 1000px !important;
  }

  .ghost {
    opacity: 0.5;
    background: #c8ebfb;
  }

  .list-enter-active, .list-leave-active {
    transition: all 0.2s;
  }
  .list-enter-from, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateX(10px);
  }

  .component-fade-leave-from {
    display:none;
  }

  .component-fade-leave-active {
    transition: all 0s;
  }
  .component-fade-enter-from {
    opacity: 0;
  }

  .component-fade-enter-active {
    transition: all 0.2s ease;
  }
</style>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>