<template>
  <div>
    <tiptap
      v-model="answer"
      style="width: 100%"
      @update="answer = $event; this.update()"
    />
  </div>
</template>

<script>
import Tiptap from '@/components/Tiptap.vue'
export default {
  name: "DescriptionField",
  props: ["question", "qid", "value", "properties"],
  components: {Tiptap},
  emits: ["update", "uncollapse"],
  data() {
    return {
      answer: this.properties.content,
      invalid: false,
    };
  },
  created() {
    if (this.answer === undefined)
      this.answer = "";
  },
  methods: {
    update() {
      this.$emit("update", {
        field_type: "text_exposition",
        content: this.answer,
      });
    },
  },
};
</script>
