<template>
  <div>
    <div class="level is-flex-grow-1 is-align-items-flex-start">
      <div class="level-left is-flex-grow-1 is-justify-content-center">
        <div class="columns is-centered is-mobile is-flex-grow-1">
          <div class="column level-item is-flex-grow-2">
            <draggable
              v-model="result.options"
              tag="transition-group"
              
              :component-data="{
                name: 'list',
                tag: 'div'}"
              :move="move()"
              v-bind="dragOptions"
              handle=".draggable"
              item-key="id"
              class="is-flex-grow-1"
              @end="update()"
            >
              <template #item="{element, index}">
                <div class="field has-addons is-rounded checkbox-field is-flex-grow-1">
                  <div
                    class="control"
                    :class="{'draggable' : result.options.length > 1}"
                  >
                    <span
                      v-show="result.options.length > 1"
                      class="button is-static "
                      :class="element.default ? 'is-primary is-light' : ''"
                    >
                      <span class="icon">
                        <i class="fas fa-ellipsis-v" />
                      </span>
                    </span>
                    <span
                      v-show="result.options.length <= 1"
                      class="button is-static"
                      :class="element.default ? 'is-primary is-light' : ''"
                    >
                      <span class="icon">
                        <i class="fas fa-ban" />
                      </span>
                    </span>
                  </div>
                  <div class="control is-expanded">
                    <Input
                      :id="qid+'_option_'+index"
                      v-model="element.question"
                      type="text"
                      required
                      class="input is-expanded"
                      placeholder="Texto da opo"
                      @input="update"
                    />
                  </div>
                  <div
                    class="control"
                    @click="deleteOption(index)"
                  >
                    <button
                      type="button"
                      class="button is-danger"
                      :disabled="result.options.length <= 1"
                    >
                      <span class="icon">
                        <i class="fas fa-trash-alt" />
                      </span>
                    </button>
                  </div>
                </div>
              </template>
            </draggable>
            <div
              class="component level-item mt-3"
            >
              <button
                type="button"
                class="button round is-primary is-light"
                @click="result.options.push(newElement()); update()"
              >
                <span
                  class="icon"
                  aria-label="Adicionar Opo"
                >
                  <i class="fas fa-plus" />
                </span>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="level-right is-flex-grow-1 level is-mobile">
        <div class="is-mobile is-flex-grow-1 flex column la vca">
          <FormInvalidator 
            :valid="valid"
            :invalid-string="invalidString"
            :start-invalid="triedSubmit"
            @invalid="$emit('uncollapse'); triedSubmit = true;"
          />
          <Dropdown
            :text="'Opes avanadas'"
            :valid="!(triedSubmit && !valid)"
          >
            <div class="control dropdown-item">
              <span class="label">Selecionados por defeito: </span>
              <div
                v-for="option in result.options"
                :key="option.id"
                class=""
              >
                <label
                  class="checkbox prevent-overflow is-small"
                  :for="qid+'_default_'+option.id"
                >
                  <input 
                    :id="qid+'_default_'+option.id"
                    v-model="option.default"
                    :value="option.id"
                    type="checkbox"
                    class="checkbox"
                    @change="update()"
                  >{{ option.question }}<i v-if="option.question == ''">Opo sem texto</i> </label>
              </div>
            </div>
            <hr class="dropdown-divider">
            <div class="control dropdown-item">
              <label
                class="label"
                :for="qid+'_min_selected'"
              >Mnimo selecionados: </label>
              <Input
                :id="qid+'_min_selected'"
                ref="min_selected"
                v-model.number="result.min_selected"
                class="input is-small"
                type="number"
                step="1"
                min="1"
                @input="update();"
                @invalid="$emit('uncollapse'); triedSubmit = true"
              />
            </div>
            <div class="control dropdown-item">
              <label
                class="label"
                :for="qid+'_max_selected'"
              >Mximo selecionados: </label>
              <Input
                :id="qid+'_max_selected'"
                ref="max_selected"
                v-model.number="result.max_selected"
                class="input is-small"
                type="number"
                step="1"
                min="1"
                @input="update()"
                @invalid="$emit('uncollapse'); triedSubmit = true"
              />
            </div>
            <hr class="dropdown-divider">
            <div class="control dropdown-item">
              <Switch
                v-model="result.allowed_other"
                :text="'Permitir a resposta \'outro\''"
                @update:modelValue="update()"
              />
            </div>
                
            <div v-show="result.allowed_other">
              <hr class="dropdown-divider">
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_other_string'"
                >Pergunta de outros: </label>
                <Input
                  :id="qid+'_other_string'"
                  v-model="result.other_string"
                  class="input is-small"
                  placeholder="Exemplo: 'Outra opo:' "
                  type="text"
                  @input="update()"
                  @invalid="$emit('uncollapse'); triedSubmit = true"
                />
              </div>
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_placeholder'"
                >Texto de sugesto (outros): </label>
                <Input
                  :id="qid+'_placeholder'"
                  v-model="result.placeholder"
                  placeholder="Texto de sugesto, tal como este"
                  class="input is-small"
                  type="text"
                  @input="update()"
                  @invalid="$emit('uncollapse'); triedSubmit = true"
                />
              </div>
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_min_length'"
                >Tamanho mnimo (outros): </label>
                <Input
                  :id="qid+'_min_length'"
                  ref="min_length"
                  v-model.number="result.min_length"
                  placeholder="Deve ser inferior ou igual ao mximo"
                  class="input is-small"
                  type="number"
                  step="1"
                  min="1"
                  @input="update()"
                  @invalid="$emit('uncollapse'); triedSubmit = true"
                />
              </div>
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_max_length'"
                >Tamanho mximo (outros): </label>
                <Input
                  :id="qid+'_max_length'"
                  ref="max_length"
                  v-model.number="result.max_length"
                  class="input is-small"
                  placeholder="Deve ser superior ou igual ao mnimo"
                  type="number"
                  step="1"
                  min="1"
                  @input="update()"
                />
              </div>
            </div>
          </Dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Switch from "@/components/Switch";
import FormInvalidator from "@/components/FormInvalidator";
import Dropdown from "@/components/Dropdown";
import Input from "@/components/Input";
import draggable from "vuedraggable";
export default {
  name: "CheckBoxField",
  components : {draggable, Dropdown, Switch, FormInvalidator, Input},
  props: ["qid", "properties"],
  emits: ["update", "uncollapse"],
  data() {
    return {
      result: this.properties,
      valid: true,
      invalidString: "",
      triedSubmit: false
    };
  },
  created() {
    if (this.result.options === undefined)
      this.result.options = [this.newElement()];
  },
  methods: {
    move() {
      return (event) => {
        return (event.related.classList.contains("checkbox-field"))
      }
    },
    deleteOption(index) {
      if (this.result.options.length > 1) {
        this.result.options.splice(index, 1);
        this.update();
      }
    },
    newElement() {
      return {'id' : this.getUid(), 'question' : ''}
    },
    getUid() {
      return this.$store.getters.getUid;
    },
    update() {
      this.valid = !this.result.min_length || !this.result.max_length || this.result.min_length <= this.result.max_length;
      if (!this.valid)
        this.invalidString = "O tamanho mnimo da resposta de \"outros\" deve ser inferior ou igual ao tamanho mximo.";
      else {
        this.valid = (this.valid) && (this.valid = !this.result.min_selected || !this.result.max_selected || this.result.min_selected <= this.result.max_selected)
        if (!this.valid)
          this.invalidString = "O nmero mnimo de opes selecionadas deve ser inferior ou igual ao nmero mximo.";
        else {
          this.valid = this.$refs['min_selected'].validity_valid && this.$refs['max_selected'].validity_valid;
          if (this.valid && this.result.allowed_other)
            this.valid = this.$refs['min_length'].validity_valid && this.$refs['max_length'].validity_valid;
          this.invalidString = " ";
        }
      }
      if (this.result.placeholder === "" || !this.result.allowed_other)
        this.result.placeholder = undefined;
      if (this.result.min_length === "" || !this.result.allowed_other)
        this.result.min_length = undefined;
      if (this.result.max_length === "" || !this.result.allowed_other)
        this.result.max_length = undefined;
      if (this.result.min_selected === "")
        this.result.min_selected = undefined;
      if (this.result.max_selected === "")
        this.result.max_selected = undefined;
      if (this.result.other_string === "" || !this.result.allowed_other)
        this.result.other_string = undefined;
      this.$emit("update", this.result);
    },
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      };
    }
  }
};
</script>
