<template>
  <div
    class="flex column la vca"
  >
    <FormInvalidator 
      :valid="valid"
      :invalid-string="invalidString"
      :start-invalid="triedSubmit"
      @invalid="$emit('uncollapse'); triedSubmit = true;"
    />
    <Dropdown
      :text="'Opes avanadas'"
      :valid="!(triedSubmit && !valid)"
    >
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_default'"
        >Valor por defeito: </label>
        <Input
          :id="qid+'_default'"
          v-model="result.default"
          class="input is-small"
          placeholder="O valor inicial"
          type="text"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_placeholder'"
        >Texto de sugesto: </label>
        <Input
          :id="qid+'_placeholder'"
          v-model="result.placeholder"
          placeholder="Texto de sugesto, tal como este"
          class="input is-small"
          type="text"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_min_length'"
        >Tamanho mnimo: </label>
        <Input
          :id="qid+'_min_length'"
          ref="min_length"
          v-model.number="result.min_length"
          placeholder="Deve ser inferior ou igual ao mximo"
          class="input is-small"
          type="number"
          step="1"
          min="1"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_max_length'"
        >Tamanho mximo: </label>
        <Input
          :id="qid+'_max_length'"
          ref="max_length"
          v-model.number="result.max_length"
          class="input is-small"
          placeholder="Deve ser superior ou igual ao mnimo"
          type="number"
          step="1"
          min="1"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_default_lines'"
        >Linhas de escrita: </label>
        <Input
          :id="qid+'_default_lines'"
          v-model.number="result.default_lines"
          class="input is-small"
          placeholder="O tamanho da caixa de escrita"
          type="number"
          step="1"
          min="1"
          @input="update"
        />
      </div>
    </Dropdown>
  </div>
</template>

<script>
import FormInvalidator from '@/components/FormInvalidator'
import Dropdown from '@/components/Dropdown'
import Input from "@/components/Input"
export default {
  name: "LongTextField",
  props: ["qid", "properties"],
  components: {FormInvalidator, Dropdown, Input},
  emits: ["update", "uncollapse"],
  data() {
    return {
      result: this.properties,
      valid: true,
      triedSubmit: false,
      invalidString : ""
    };
  },
  methods: {
    update() {
      this.valid = !this.result.min_length || !this.result.max_length || this.result.min_length <= this.result.max_length
      if (!this.valid)
        this.invalidString = "O tamanho mnimo da resposta deve ser inferior ou igual ao tamanho mximo.";
      else {
        this.valid = this.$refs['min_length'].validity_valid && this.$refs['max_length'].validity_valid;
        this.invalidString = " ";
      }
      if (this.result.default === "")
        this.result.default = undefined;
      if (this.result.placeholder === "")
        this.result.placeholder = undefined;
      if (this.result.min_length === "")
        this.result.min_length = undefined;
      if (this.result.max_length === "")
        this.result.max_length = undefined;
      if (this.result.default_lines === "")
        this.result.default_lines = undefined;
      this.$emit("update", this.result);
    },
  },
};
</script>

<style type="scss">
  .option-label {
    display: inline-block;
    width: 145px;
    white-space: nowrap;
  }
</style>
