<template>
  <div>
    <div
      v-if="editor"
      class="level mt-3 mb-1"
    >
      <div class="level-item">
        <button
          type="button"
          class="button"
          :class="{ 'is-active': editor.isActive({ textAlign: 'left' }) }"
          aria-label="Alinhar  esquerda"
          title="Alinhar  esquerda"
          @click="editor.chain().focus().setTextAlign('left').run()"
        >
          <span class="icon"><i class="fas fa-align-left" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-active': editor.isActive({ textAlign: 'center' }) }"
          aria-label="Alinhar ao centro"
          title="Alinhar ao centro"
          @click="editor.chain().focus().setTextAlign('center').run()"
        >
          <span class="icon"><i class="fas fa-align-center" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-active': editor.isActive({ textAlign: 'right' }) }"
          aria-label="Alinhar  direita"
          title="Alinhar  direita"
          @click="editor.chain().focus().setTextAlign('right').run()"
        >
          <span class="icon"><i class="fas fa-align-right" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-active': editor.isActive({ textAlign: 'justify' }) }"
          aria-label="Justificar"
          title="Justificar"
          @click="editor.chain().focus().setTextAlign('justify').run()"
        >
          <span class="icon"><i class="fas fa-align-justify" /></span>
        </button>
      </div>
      <div class="level-item">
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('bold') }"
          aria-label="Negrito"
          title="Negrito"
          @click="editor.chain().focus().toggleBold().run()"
        >
          <span class="icon"><i class="fas fa-bold" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('italic') }"
          aria-label="Itlico"
          title="Itlico"
          @click="editor.chain().focus().toggleItalic().run()"
        >
          <span class="icon"><i class="fas fa-italic" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('underline') }"
          aria-label="Sublinhado"
          title="Sublinhado"
          @click="editor.chain().focus().toggleUnderline().run()"
        >
          <span class="icon"><i class="fas fa-underline" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('strike') }"
          aria-label="Rasurado"
          title="Rasurado"
          @click="editor.chain().focus().toggleStrike().run()"
        >
          <span class="icon"><i class="fas fa-strikethrough" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('warning') }"
          aria-label="Aviso (vermelho)"
          title="Aviso (vermelho)"
          @click="editor.chain().focus().toggleWarning().run()"
        >
          <span
            class="icon"
            :style="editor.isActive('warning') ? 'color: #F08080;' : 'color: #B22222'"
          ><i class="fas fa-font" /></span>
        </button>
        <button
          type="button"
          class="button"
          aria-label="Limpar Formatao"
          title="Limpar Formatao"
          @click="editor.chain().focus().unsetAllMarks().run()"
        >
          <span class="icon"><i class="fas fa-eraser" /></span>
        </button>
      </div>
      <div class="level-item">
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('clickable'), 'is-static' : !editor.isActive('link') }"
          aria-label="Boto"
          title="Boto"
          @click="editor.chain().focus().toggleClickable().run()"
        >
          <span class="icon"><i class="fas fa-hand-pointer" /></span>
        </button>
        <button
          v-if="editor.isActive('link')"
          type="button"
          class="button"
          aria-label="Remover hiperligao"
          title="Remover hiperligao"
          @click="editor.chain().focus.unsetClickable().run(); editor.chain().focus().unsetLink().run();"
        >
          <span class="icon"><i class="fas fa-unlink" /></span>
        </button>
        <button
          v-else
          :class="{ 'is-active': editor.isActive('link') }"
          type="button"
          class="button"
          aria-label="Adicionar hiperligao"
          title="Adicionar hiperligao"
          @click="setLink()"
        >
          <span class="icon"><i class="fas fa-link" /></span>
        </button>
        <button
          type="button"
          class="button"
          aria-label="Adicionar imagem"
          title="Adicionar imagem"
          @click="addImage"
        >
          <span class="icon"><i class="fas fa-image" /></span>
        </button>
      </div>
      <div class="level-item">
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('paragraph') }"
          aria-label="Pargrafo"
          title="Pargrafo"
          @click="editor.chain().focus().setParagraph().run()"
        >
          <span class="icon"><i class="fas fa-paragraph" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{'is-dark' : editor.isActive('heading', { level: 3 }) }"
          aria-label="Ttulo"
          title="Ttulo"
          @click="editor.chain().focus().toggleHeading({ level: 3 }).run()"
        >
          <span class="icon"><i class="fas fa-heading" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('bulletList') }"
          aria-label="Lista de pontos"
          title="Lista de pontos"
          @click="editor.chain().focus().toggleBulletList().run()"
        >
          <span class="icon"><i class="fas fa-list-ul" /></span>
        </button>
        <button
          type="button"
          class="button"
          :class="{ 'is-dark': editor.isActive('orderedList') }"
          aria-label="Lista ordenada"
          title="Lista ordenada"
          @click="editor.chain().focus().toggleOrderedList().run()"
        >
          <span class="icon"><i class="fas fa-list-ol" /></span>
        </button>
        <button
          type="button"
          class="button"
          aria-label="Linha horizontal"
          title="Linha horizontal"
          @click="editor.chain().focus().setHorizontalRule().run()"
        >
          <span class="icon"><i class="fas fa-ruler-horizontal" /></span>
        </button>
      </div>
      <div class="level-item">
        <button
          type="button"
          class="button"
          aria-label="Desfazer"
          title="Desfazer"
          @click="editor.chain().focus().undo().run()"
        >
          <span class="icon"><i class="fas fa-undo" /></span>
        </button>
        <button
          type="button"
          class="button"
          aria-label="Refazer"
          title="Refazer"
          @click="editor.chain().focus().redo().run()"
        >
          <span class="icon"><i class="fas fa-redo" /></span>
        </button>
      </div>
    </div>
    <editor-content
      v-model="content"
      :editor="editor"
      class="p-3 custom-html content"
      aria-label="Itlico"
      style="border: #121212 solid 1px; border-radius: 5px;"
    />
  </div>
</template>

<script>
import { Editor, EditorContent } from '@tiptap/vue-3'
import Link from '@tiptap/extension-link'
import TextAlign from '@tiptap/extension-text-align'
import Dropcursor from '@tiptap/extension-dropcursor'
import Image from '@tiptap/extension-image'
import Underline from '@tiptap/extension-underline'
import {
  Mark,
  markInputRule,
  markPasteRule,
  mergeAttributes
} from '@tiptap/core'
import StarterKit from '@tiptap/starter-kit'

export const starInputRegex = /(?:^|\s)((?:\*\*)((?:[^*]+))(?:\*\*))$/gm
export const starPasteRegex = /(?:^|\s)((?:\*\*)((?:[^*]+))(?:\*\*))/gm
export const underscoreInputRegex = /(?:^|\s)((?:__)((?:[^__]+))(?:__))$/gm
export const underscorePasteRegex = /(?:^|\s)((?:__)((?:[^__]+))(?:__))/gm

const Warning = Mark.create({
  name : 'warning',

  defaultOptions: {
    HTMLAttributes: {},
  },

  parseHTML() {
    return [
      {
        tag: 'warning',
      }
    ]
  },

  renderHTML({ HTMLAttributes }) {
    return ['warning', mergeAttributes(this.options.HTMLAttributes, HTMLAttributes), 0]
  },

  addCommands() {
    return {
      setWarning: () => ({ commands }) => {
        return commands.setMark('warning')
      },
      toggleWarning: () => ({ commands }) => {
        return commands.toggleMark('warning')
      },
      unsetWarning: () => ({ commands }) => {
        return commands.unsetMark('warning')
      },
    }
  },

  addInputRules() {
    return [
      markInputRule(starInputRegex, this.type),
      markInputRule(underscoreInputRegex, this.type),
    ]
  },

  addPasteRules() {
    return [
      markPasteRule(starPasteRegex, this.type),
      markPasteRule(underscorePasteRegex, this.type),
    ]
  },
})

const Clickable = Mark.create({
  name : 'clickable',

  defaultOptions: {
    HTMLAttributes: {},
  },

  parseHTML() {
    return [
      {
        tag: 'clickable'
      }
    ]
  },

  renderHTML({ HTMLAttributes }) {
    return ['clickable', mergeAttributes(this.options.HTMLAttributes, HTMLAttributes), 0]
  },

  addCommands() {
    return {
      setClickable: () => ({ commands }) => {
        return commands.setMark('clickable')
      },
      toggleClickable: () => ({ commands }) => {
        return commands.toggleMark('clickable')
      },
      unsetClickable: () => ({ commands }) => {
        return commands.unsetMark('clickable')
      },
    }
  },

  addInputRules() {
    return [
      markInputRule(starInputRegex, this.type),
      markInputRule(underscoreInputRegex, this.type),
    ]
  },

  addPasteRules() {
    return [
      markPasteRule(starPasteRegex, this.type),
      markPasteRule(underscorePasteRegex, this.type),
    ]
  },
})

export default {
  props: ['modelValue'],
  emits: ['update'],
  components: {
    EditorContent,
  },

  data() {
    return {
      editor: null,
      content: this.modelValue ? this.modelValue : ""
    }
  },
  mounted() {
    this.editor = new Editor({
      extensions: [
        StarterKit,
        Warning,
        Link,
        TextAlign.configure({
          types: ['heading', 'paragraph'],
        }),
        Clickable,
        Image,
        Dropcursor,
        Underline
      ],
      onUpdate: () => {
        this.$emit('update', this.editor.getHTML());
      },
      content: this.content,
    });
  },
  methods: {
    setLink() {
      const url = window.prompt('Insira a hiperligao: ');
      if (url != null)
        this.editor.chain().focus().setLink({ href: url }).run();
    },
    addImage() {
      const url = window.prompt('Hiperligao da imagem: ')

      if (url) {
        this.editor.chain().focus().setImage({ src: url }).run()
      }
    },
  },
  beforeUnmount() {
    this.editor.destroy()
  },
}
</script>

<style lang="scss">
/* Basic editor styles */
</style>