<template>
  <div>
    <transition
      name="fade" 
      mode="out-in"
    >
      <div>
        <draggable
          v-show="!collapsed"
          v-model="section.questions"
          item-key="id"
          group="options"
          tag="transition-group"
          :component-data="{
            name: 'list',
            tag: 'ul'
          }"
          :move="move()"
          v-bind="dragOptions"
          handle=".panel-heading"
          class="draggable-question drag-to-safety"
          @end="update()"
        >
          <template #item="{element, index}"> 
            <div
              :ref="'question_' + index"
              class="panel-block is-flex is-align-items-flex-start list-item draggable-question mr-0"
              style="min-height: 100px;"
            >
              <div class="panel is-fullwidth is-flex-grow-1">
                <div 
                  class="panel-heading draggable level mb-0" 
                  style="background-color: #ededed; color: #363636;"
                >
                  <div
                    class="level mb-0"
                    style="max-width: 100%;"
                  >
                    <h3
                      :class="{'required' : section.questions[index].required}"
                      class="prevent-overflow"
                    >
                      <span><template v-if="!hideQuestionNumbers">{{ index+1 }}. </template>{{ element.question }}</span> ({{ getFieldTypeTitle(element.properties.field_type) }})
                    </h3>
                  </div>
                  <fieldset class="level is-hidden-mobile">
                    <Switch 
                      v-if="!element.properties.field_type.includes('_exposition')"
                      v-model="element.required"
                      :text="'Obrigatria'"
                      class="mb-0 mr-4 subtitle"
                      @update:modelValue="update()"
                    />
                    <button
                      type="button"
                      class="button is-danger mr-2"
                      aria-label="Apagar pergunta"
                      @click="section.questions.splice(index, 1); update()"
                    >
                      <span class="icon">
                        <i class="fas fa-trash-alt" />
                      </span>
                    </button>
                    <button
                      v-show="section.hidden_elements[element.id]"
                      type="button"
                      class="button"
                      aria-label="Expandir pergunta"
                      @click="section.hidden_elements[element.id]=undefined"
                    >
                      <span
                        class="icon"
                      >
                        <i class="fas fa-chevron-down" />
                      </span>
                    </button>
                    <button
                      v-show="!section.hidden_elements[element.id]"
                      type="button"
                      class="button"
                      aria-label="Minimizar pergunta"
                      @click="section.hidden_elements[element.id]=true"
                    >
                      <span
                        class="icon"
                      >
                        <i class="fas fa-chevron-up" />
                      </span>
                    </button>
                  </fieldset>
                </div>
                <div v-show="!section.hidden_elements[element.id]">
                  <fieldset class="level is-mobile panel-block is-hidden-tablet mb-0">
                    <div class="level-left">
                      <Switch 
                        v-if="!element.properties.field_type.includes('_exposition')" 
                        v-model="element.required"
                        :text="'Obrigatria'"
                        class="mb-0 mr-4 is-small level-item"
                        @update:modelValue="update()"
                      />
                    </div>
                    <button
                      type="button"
                      class="button is-danger mr-2 level-item"
                      aria-label="Apagar pergunta"
                      @click="section.questions.splice(index, 1); update()"
                    >
                      <span class="icon">
                        <i class="fas fa-trash-alt" />
                      </span>
                    </button>
                  </fieldset>
                  <div
                    class="panel-block"
                  >
                    <Input
                      :id="'question_'+index"
                      v-model="element.question"
                      type="text"
                      :placeholder="element.properties.field_type.includes('_exposition') ? 'Ttulo da exposio' : 'Ttulo da pergunta'"
                      class="input"
                      required
                      @input="update()"
                      @invalid="$emit('uncollapse'); section.hidden_elements[element.id] = false"
                    />
                  </div>
                  <component
                    :is="element.properties.field_type"
                    class="panel-block"
                    :question="element"
                    :qid="index"
                    :properties="element.properties"
                    @update="element.properties = $event; update()"
                    @uncollapse="$emit('uncollapse'); section.hidden_elements[element.id] = false"
                  />
                </div>
              </div>
            </div>
          </template>
        </draggable>
        <div>
          <div>
            <transition
              name="component-fade" 
              mode="out-in"
            >
              <div
                v-show="selecting_field && !collapsed"
                class="level pt-5 pl-6 pr-6 pb-3"
              >
                <div class="select level-item m-1">
                  <select
                    v-model="new_field"
                    class="input"
                    @change="selected_field()"
                  >
                    <option
                      disabled
                      value="none"
                    >
                      Texto
                    </option>
                    <option value="shorttext">
                      Texto Curto
                    </option>
                    <option value="longtext">
                      Texto Longo
                    </option>
                    <option value="email">
                      Email
                    </option>
                  </select>
                </div>
                <div class="select level-item m-1">
                  <select
                    v-model="new_field"
                    class="input"
                    @change="selected_field()"
                  >
                    <option
                      disabled
                      value="none"
                    >
                      Numrico
                    </option>
                    <option value="number">
                      Nmero
                    </option>
                    <option value="nif">
                      NIF
                    </option>
                    <option value="phone">
                      Nmero telefnico
                    </option>
                  </select>
                </div>
                <div class="select level-item m-1">
                  <select
                    v-model="new_field"
                    class="input"
                    @change="selected_field()"
                  >
                    <option
                      disabled
                      value="none"
                    >
                      Data
                    </option>
                    <option value="date">
                      Data
                    </option>
                    <option value="datetime">
                      Data e Hora
                    </option>
                  </select>
                </div>
                <div class="select level-item m-1">
                  <select
                    v-model="new_field"
                    class="input"
                    @change="selected_field()"
                  >
                    <option
                      disabled
                      value="none"
                    >
                      Seleo
                    </option>
                    <option value="multiplechoice">
                      Escolha Mltipla
                    </option>
                    <option value="selectboxes">
                      Caixas de Seleo
                    </option>
                  </select>
                </div>
                <div class="select level-item m-1">
                  <select
                    v-model="new_field"
                    class="input"
                    @change="selected_field()"
                  >
                    <option
                      disabled
                      value="none"
                    >
                      Exposio
                    </option>
                    <option value="text_exposition">
                      Texto e Imagens
                    </option>
                    <option value="youtube_exposition">
                      Vdeo (YouTube)
                    </option>
                  </select>
                </div>
              </div>
            </transition>
            <transition
              name="component-fade" 
              mode="out-in"
            >
              <div
                v-show="!selecting_field && !collapsed"
                class="component flex row ca p-4 mb-1"
              >
                <button
                  type="button"
                  class="button round is-large is-primary is-light"
                  aria-label="Adicionar pergunta"
                  @click="selecting_field = true"
                >
                  <span class="icon">
                    <i class="fas fa-plus" />
                  </span>
                </button>
              </div>
            </transition>
            <transition
              name="component-fade" 
              mode="out-in"
            >
              <div
                v-show="collapsed"
                class="subtitle flex row ca pb-3"
              >
                {{ section.questions.length }} {{ section.questions.length != 1 ? 'perguntas' : 'pergunta' }}
              </div>
            </transition>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import Switch from "@/components/Switch";
  import Input from "@/components/Input"
  import draggable from "vuedraggable";
  import * as fieldCreators from "@/fieldCreators";
  import {getFieldTypeTitle} from "@/common.js"
  export default {
    name: "Question",
    props : ["questionSection", "text", "collapsed", "hideQuestionNumbers"],
    emits : ["uncollapse", "update"],
    components : Object.assign({}, {Switch, draggable, Input}, fieldCreators),
    data() {
      return {
        section : this.questionSection,
        selecting_field : false,
        new_field : "none",
      }
    },
    created() {
      if (this.section.hidden_elements === undefined)
        this.section.hidden_elements = {};
    },
     methods : {
      selected_field() {
        this.selecting_field = false;
        var newField = {"id" : this.$store.getters.getUid, "properties": {"field_type" : this.new_field}};
        if (this.new_field === 'multiplechoice')
          newField.properties.style = "radio";
        else if (this.new_field === 'number')
          newField.properties.style = "text";
        this.section.questions.push(newField);
        this.new_field = "none";
        this.scrollToLast();
        this.update();
      },
      update() {
        this.$emit("update", this.sections);
      },
      move() {
        return (event) => {
          return (event.related.classList.contains("draggable-question"))
        }
      },
      getFieldTypeTitle(field_type) {
        return getFieldTypeTitle(field_type);
      },
      scrollToLast() {
        let last = 'question_'.concat(this.section.questions.length-1);
        this.$nextTick(() => this.$refs[last].scrollIntoView({behavior: 'smooth'}));
      }
    },
    computed: {
      dragOptions() {
        return {
          animation: 200,
          group: "description",
          disabled: false,
          ghostClass: "ghost"
        };
      }
    }
  };
</script>