<template>
  <div
    class="flex column la vca"
  >
    <FormInvalidator 
      :valid="valid"
      :start-invalid="triedSubmit"
      :invalid-string="invalidString"
      @invalid="$emit('uncollapse'); triedSubmit = true;"
    />
    <Dropdown
      :text="'Opes avanadas'"
      :valid="!(triedSubmit && !valid)"
    >
      <div class="dropdown-item">
        <label
          class="label"
          :for="qid + '_style'"
        >Estilo: </label>
        <div class="control has-icons-left">
          <div class="select">
            <select
              :id="qid + '_style'"
              v-model="result.style"
              @change="update()"
            >
              <option value="text">
                Texto
              </option>
              <option value="slider">
                Barra deslizante
              </option>
            </select>
          </div>
          <span
            v-show="result.style == 'text'"
            class="icon is-left"
          >
            <i class="fas fa-font" />
          </span>
          <span
            v-show="result.style == 'slider'"
            class="icon is-left"
          >
            <i class="fas fa-sliders-h" />
          </span>
        </div>
      </div>
      <hr class="dropdown-divider">
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_placeholder'"
        >Texto de sugesto: </label>
        <Input
          :id="qid+'_placeholder'"
          v-model="result.placeholder"
          placeholder="Texto de sugesto, tal como este"
          class="input is-small"
          type="text"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_default'"
        >Valor por defeito: </label>
        <Input
          :id="qid+'_default'"
          ref="default"
          v-model.number="result.default"
          placeholder="Valor inicial"
          class="input is-small"
          step="any"
          type="number"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_prefix'"
        >Prefixo: </label>
        <Input
          :id="qid+'_prefix'"
          v-model="result.prefix"
          class="input is-small"
          placeholder="O texto que aparece antes do nmero"
          type="text"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_suffix'"
        >Sufixo: </label>
        <Input
          :id="qid+'_suffix'"
          v-model="result.suffix"
          class="input is-small"
          placeholder="O texto que aparece depois do nmero"
          type="text"
          @input="update"
        />
      </div>
      <hr class="dropdown-divider">
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_decimal_places'"
        >Casas decimais: </label>
        <Input
          :id="qid+'_decimal_places'"
          ref="decimal_places"
          v-model.number="result.decimal_places"
          placeholder="Nmero mximo de casas decimais"
          class="input is-small"
          min="0"
          type="number"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :class="{'required' : result.style==='slider'}"
          :for="qid+'_min'"
        >Valor mnimo: </label>
        <Input
          :id="qid+'_min'"
          ref="min"
          v-model.number="result.min"
          placeholder="Deve ser inferior ou igual ao mximo"
          class="input is-small"
          type="number"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :class="{'required' : result.style==='slider'}"
          :for="qid+'_max'"
        >Valor mximo: </label>
        <Input
          :id="qid+'_max'"
          ref="max"
          v-model.number="result.max"
          class="input is-small"
          placeholder="Deve ser superior ou igual ao mnimo"
          type="number"
          @input="update"
        />
      </div>
    </Dropdown>
  </div>
</template>

<script>
import FormInvalidator from '@/components/FormInvalidator'
import Dropdown from '@/components/Dropdown'
import Input from '@/components/Input'
export default {
  name: "NumberField",
  props: ["qid", "properties"],
  emits: ["update", "uncollapse"],
  components: {FormInvalidator, Dropdown, Input},
  data() {
    return {
      result: this.properties,
      valid: true,
      triedSubmit: false,
      invalidString: ' '
    };
  },
  created() {
    if (this.result.style === undefined)
      this.result.style = "text";
  },
  methods: {
    update() {
      this.valid = this.result.min === undefined || this.result.max === undefined;
      if (!this.valid) {
        this.valid = this.result.min <= this.result.max
        if (!this.valid) {
          this.invalidString = 'O mximo deve ser superior ao mnimo';
        }
      }
      else if (this.result.style === 'slider') {
        this.valid = false;
        this.invalidString = 'A barra deslizante requer um mnimo e um mximo'
      }
      if (this.valid) { 
        this.valid = this.$refs['default'].validity_valid && this.$refs['min'].validity_valid && this.$refs['max'].validity_valid && this.$refs['decimal_places'].validity_valid && this.$refs['default'].validity_valid;
        this.invalidString = ' ';
      }
      if (this.result.default === "")
        this.result.default = undefined;
      if (this.result.placeholder === "")
        this.result.placeholder = undefined;
      if (this.result.min === "")
        this.result.min = undefined;
      if (this.result.max === "")
        this.result.max = undefined;
      if (this.result.decimal_places === "")
        this.result.decimal_places = undefined;
      if (this.result.prefix === "")
        this.result.prefix = undefined;
      if (this.result.suffix === "")
        this.result.suffix = undefined;
      this.$emit("update", this.result);
    },
  },
};
</script>

<style type="scss">
  .option-label {
    display: inline-block;
    width: 145px;
    white-space: nowrap;
  }
</style>
